
img{
  border-radius: 12px;
  height:400px;
  width: 350px;
 
}

#photo{
  
  margin-left: 700px;
  
}

h1{
  
  font-family: 'Staatliches', cursive;
  font-size: 100px;
  margin-top: 200px;
  margin-left: 200px;
  padding: 0px;
  margin-bottom: 0px;
  height: 0px;
}

body{
  margin:0px;
}



.nav-items{
  font-family: 'Lato', sans-serif;
font-family: 'Montserrat', sans-serif;
  transition: color .3s ease-in-out, box-shadow .3s ease-in-out;
}

.nav-items:hover {
  box-shadow: inset 100px 0 0 0 #54b3d6;
  color: white;
}

nav{
  display:flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background-color:white;
  color:black;
  
}  

.nav-links{
    width:40%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style: none;
    

}
li{
  cursor: pointer;
}
h3{
  font-family: 'Lato', sans-serif;
font-family: 'Montserrat', sans-serif;
}
.about-description{
  font-family: 'Lato', sans-serif;
font-family: 'Montserrat', sans-serif;
margin-top: 100px;
margin-left: 100px;
font-size: 30px;
margin-right: 100px;
}

.about-section{
  font-family: 'Lato', sans-serif;
font-family: 'Montserrat', sans-serif;
margin:0 auto;
display:flex;
text-align: left;

}
#introduction{
  font-family: 'Lato', sans-serif;
font-family: 'Montserrat', sans-serif;
font-size: 30px;
margin-bottom: 0px;
margin-left: 100px;
}

hr{
  width:500px;
  border: 2px solid black;
}

#project-title{
  text-align: center;
  font-family: 'Lato', sans-serif;
font-family: 'Montserrat', sans-serif;
font-size: 30px;
}

.project-item{
  list-style: none;
  font-family: 'Lato', sans-serif;
  
font-family: 'Montserrat', sans-serif;
margin-left: 350px;
font-size: 20px;
}

.contact-form{
margin: 0 auto;
padding: 30px;
align-items: center;
height: 350px;
width: 500px;
border: 2px solid black;
margin-top: 50px;

}
 input{
  width:80%;
  height: 30px;
  padding: 12px;
  font-size: 20px;
 }

 textarea {
  width: 100%;
  height: 150px;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 2px solid black;
  border-radius: 4px;
  
  font-size: 20px;
  resize: none;
}

button{
padding: 15px 32px;
align-self: right;

}